

var tml = gsap.timeline({ defaults: { duration: 1 } })
tml.fromTo("body", { opacity: 0, backgroundColor:'black'}, { opacity: 1, ease: "Power1.easeInOut", delay: 1 },)
    .fromTo(".nav_component", { opacity: 0, y:-100 }, { opacity: 1, y:0, stagger: 0.1, ease: "Power1.easeInOut"}, "-=0.2")
    .fromTo(".nav_logo", { rotate: 0 }, { rotate: 360, ease: "Power1.easeInOut" }, "-=0.6")
    .fromTo(".header_wrapper", { opacity: 0, y: 50 }, { opacity: 1, ease: "Power1.easeInOut", y: 0 }, "-=0.8")
    .fromTo(".header_lottie-component", { opacity: 0, y: 600 }, { opacity: 1, ease: "Power2.easeInOut", y: 0 }, "-=0.9")
    .to(".sup", { "clip-path": "polygon(100% 0%, 0% 0%, 0% 125%, 100% 100%)",
    x: 0,
    duration: 1.8,})



